<script>
  import { onMount } from "svelte";
  import FormManager from "./FormManager.svelte";
  import { v4 as uuidv4 } from "uuid";
  import { NotificationDisplay, notifier } from "@beyonk/svelte-notifications";
  import { createPersona } from "./queries/create-persona.js";
  export let hidePersona;
  let uuid = "";
  onMount(() => {
    uuid = uuidv4();
  });
</script>

<style>
  main {
    z-index: 9999999;
    font-size: 0.7rem;
    font-weight: 600;
    padding: 1rem 1rem;
    background-color: #eeedef;
    position: absolute;
    bottom: 50%;
    right: 50%;
    border-radius: 10px;
    border-color: white;
    color: #575557;
    border: 2px solid white;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.26);
    transform: translate(50%, 50%);
  }
</style>

<NotificationDisplay />
<main>
  <h1>Cadastro de Persona</h1>

  <FormManager
    type={'NovaPersona'}
    onSubmit={data => {
      const values = { ...data.values };
      const fields = { ...data.fields };
      const uuid = uuidv4();
      createPersona({ ...values, uuid, fields })
        .then(() => {
          notifier.success('Persona Criada');
          hidePersona();
        })
        .catch(() => {
          notifier.error('Erro!');
        });
    }}
    {uuid}
    cancelFn={hidePersona}
    addForm={() => {}} />
</main>
