<script>
  import { createEventDispatcher } from "svelte";
  import clsx from "clsx";
  // Declar variables.
  export let id = "";
  export let name = "";
  export let classe = "";
  export let disabled = null;
  export let journals = [];
  export let accounts = [];
  const dispatch = createEventDispatcher();
  function uuidv4() {
    return "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(/[xy]/g, function(c) {
      var r = (Math.random() * 16) | 0,
        v = c == "x" ? r : (r & 0x3) | 0x8;
      return v.toString(16);
    });
  }
  let entries = [];

  // Change value field.
  const addJournalEntry = () => {
    entries = [
      ...entries,
      { nature: "D", account: null, frontendUUID: uuidv4() }
    ];
  };
  function onChangerValue(event, frontendUUID) {
    let id = event.target.parentNode.id;
    console.log(event.target.name, event.target.value);
    entries = entries.map(entry => {
      if (entry.frontendUUID === frontendUUID) {
        let obj = {};
        obj[event.target.name] = event.target.value;
        return { ...entry, ...obj };
      }
      return { ...entry };
    });
    console.log(entries);
    dispatch("changeValue", {
      name: name,
      value: entries
    });
  }

  // Delete entry.
  function deleteEntry(line) {
    entries = entries.filter(entry => entry.frontendUUID != line);
    dispatch("changeValue", {
      name: name,
      value: entries
    });
  }
  $: entries = [...journals];
  $: console.log("[entries]", entries);
</script>

<style>
  .list-files .file {
    display: flex;
  }
  .list-files .file .img,
  .list-files .file .infos {
    width: 50%;
  }
  .list-files .file .img img {
    width: 100%;
  }
  .journal {
    display: grid;
    grid-template-columns: repeat(3, min-content);
    justify-content: end;
  }
  select {
    font-size: 0.5rem;
    height: 2rem;
  }
  button {
    font-size: 0.5rem;
  }
</style>

<div class="journals">
  {#each entries as journal, i (journal.frontendUUID)}
    <div id={i} name="journal" class="journal">

      <select
        id={`nature${i}`}
        name="nature"
        on:input={event => onChangerValue(event, journal.frontendUUID)}>
        <option value={'D'} selected={journal.nature === 'D'}>D</option>
        <option value={'C'} selected={journal.nature === 'C'}>C</option>
      </select>

      <select
        id={`account${i}`}
        name="account"
        on:input={event => onChangerValue(event, journal.frontendUUID)}>
        {#each accounts as option (option.value)}
          <option
            value={option.value}
            selected={journal.account === option.value}>
            {option.title}
          </option>
        {:else}
          <option>Any</option>
        {/each}
      </select>

      <button
        type="button"
        on:click|preventDefault={() => {
          deleteEntry(journal.frontendUUID);
        }}>
        Remove
      </button>
    </div>
  {/each}
  <button on:click={addJournalEntry} disabled={entries.length > 5}>
    Adicionar
  </button>
</div>
