<script>
  import _ from "lodash";
  import { contentGridStore } from "./store/store.js";
  export let group;
  export let itemsOptions = [];
  export let selectedDate = -1;
  export let depth = 0;
  export let dates = [];
  export let maxDepth = 0;
  export let path = "";
  export let data = [];
  import { createEventDispatcher } from "svelte";
  export let index = 0;

  let node = null;
  let nodeChildren = null;
  const dispatch = createEventDispatcher();
  const formatter = new Intl.NumberFormat("pt-BR", {
    style: "currency",
    currency: "BRl"
  });
  const hasArray = () => group.some(g => _.isArray(g));
  const setCells = (node, depth) => {
    if (depth == maxDepth) {
      node.style[
        "grid-template-columns"
      ] = `repeat(${maxDepth},3rem) repeat(${dates.length + 1},1fr)`;
    } else if (depth === 0) {
      node.style["grid-template-columns"] = `3rem  repeat(${dates.length +
        1},1fr)`;
    } else {
      node.style[
        "grid-template-columns"
      ] = `repeat(${depth},3rem) repeat(${dates.length + 1},1fr)`;
    }
  };
  $: if (group && itemsOptions) {
    if (node && nodeChildren) {
      if (depth === maxDepth) {
        let ref = node;

        let path = ref.getAttribute("path").split("-");
        let matchOBJ = {};
        path.forEach((value, i) => {
          matchOBJ = {
            ...matchOBJ,
            [itemsOptions[i].key]: value === "S/A" ? null : value
          };
        });

        const dataF = _.filter(data, _.matches({ ...matchOBJ }));
        console.log(dataF, matchOBJ);
        const rowNumber = [].indexOf.call(
          nodeChildren.parentNode.parentNode.children,
          nodeChildren.parentNode
        );
        dataF.forEach(entry => {
          let columnNumber = -1;
          const cell = document.createElement("div");
          if (selectedDate === -1) {
            columnNumber = dates.map(d => d.year).indexOf(entry.year);
          }
          cell.style["grid-row"] = `${1}`;
          cell.style["grid-column"] = `${maxDepth + columnNumber + 2}`;
          console.log(
            "VALOR MONTANTE",
            "Coluna",
            `${maxDepth + columnNumber + 2}`
          );
          console.log("[maxDepth]", maxDepth, columnNumber, dates);
          const span = document.createElement("span");

          span.style["font-size"] = `0.65rem`;
          span.innerText = formatter.format(entry.amount);
          const year = document.createElement("span");
          year.style["font-size"] = `0.35rem`;
          //   year.innerText = `(${entry.year})`;
          // span.append(year);
          cell.append(span);

          node.append(cell);
          //$contentGridStore.append(cell);
        });
      } else {
        let ref = node;

        let path = ref.getAttribute("path").split("-");
        let matchOBJ = {};
        path.forEach((value, i) => {
          matchOBJ = {
            ...matchOBJ,
            [itemsOptions[i].key]: value === "S/A" ? null : value
          };
        });

        const dataF = _.filter(data, _.matches({ ...matchOBJ }));
        console.log("SUMBY", dataF, matchOBJ);
        const rowNumber = [].indexOf.call(
          nodeChildren.parentNode.parentNode.children,
          nodeChildren.parentNode
        );
        dates.forEach((date, columnNumber) => {
          const cell = document.createElement("div");
          let amountYear = 0;
          if (selectedDate == -1) {
            amountYear = _.sumBy(
              _.filter(dataF, _.matches({ year: date.year })),
              "amount"
            );
          }
          if (depth === 0) {
            const span = document.createElement("span");
            cell.style["grid-row"] = `${1}`;
            cell.style["grid-column"] = `${columnNumber + 3}`;
            span.innerText = date.year; //formatter.format(amountYear);
            cell.append(span);

            node.append(cell);
          } else {
            cell.style["grid-row"] = `${1}`;
            cell.style["grid-column"] = `${depth + columnNumber + 2}`;
            const span = document.createElement("span");

            span.style["font-size"] = `0.65rem`;

            if (depth === 1) {
              span.style["color"] = `blue`;
            }
            if (depth === 0) {
              span.style["color"] = `green`;
            }
            span.innerText = formatter.format(amountYear); //formatter.format(amountYear);
            cell.append(span);

            node.append(cell);
          }
        });
      }
    }
  }
  const setPosition = (node, depth) => {
    const data = group.data;
    const rowNumber = [].indexOf.call(
      node.parentNode.parentNode.children,
      node.parentNode
    );
    if (depth === maxDepth) {
      node.style["grid-column"] = `${depth + 1}`;
    } else {
      node.style["grid-column"] = `${depth + 1}`;
      if (depth === 0) {
        node.style["color"] = `green`;
      }
      if (depth === 1) {
        node.style["color"] = `blue`;
      }
    }

    // data.forEach(number => {
    //   const cell = document.createElement("div");
    //   const columnNumber = dates.map(d => d.year).indexOf(number.year);
    //   console.log(
    //     columnNumber,
    //     dates.map(d => d.year).indexOf(number.year),
    //     number,
    //     dates
    //   );
    //   cell.style["grid-row"] = `${rowNumber + 1}`;
    //   cell.style["grid-column"] = `${columnNumber + 1}`;
    //   const span = document.createElement("span");

    //   span.style["font-size"] = `0.65rem`;
    //   span.innerText = formatter.format(number.amount);
    //   cell.append(span);

    //   $contentGridStore.append(cell);
    // });
  };
</script>

<style>
  .balanco-group {
    display: grid;

    grid-auto-flow: row;
  }
</style>

<div
  class="balanco-group"
  use:setCells={depth}
  key={group.key}
  bind:this={node}
  path={path ? path + '-' + group.name : group.name}
  {depth}
  balance-group={true}>
  <span use:setPosition={depth} bind:this={nodeChildren}>{group.name}</span>
</div>
{#if _.isArray(group.children)}
  {#each group.children as g}
    <svelte:self
      group={g}
      {data}
      {selectedDate}
      {itemsOptions}
      path={path ? path + '-' + group.name : group.name}
      depth={depth + 1}
      {dates}
      {maxDepth}
      index={index + 1} />
  {/each}
{/if}
