<script>
  import { onMount } from "svelte";
  import FormManager from "./FormManager.svelte";
  import { v4 as uuidv4 } from "uuid";
  import { NotificationDisplay, notifier } from "@beyonk/svelte-notifications";
  import { createTag } from "./queries/create-tag.js";
  export let hideTag;
  let uuid = "";
  onMount(() => {
    uuid = uuidv4();
  });
</script>

<style>
  main {
    z-index: 9999999;
    font-size: 0.7rem;
    font-weight: 600;
    padding: 1rem 1rem;
    background-color: #eeedef;
    position: absolute;
    bottom: 50%;
    right: 50%;
    border-radius: 10px;
    border-color: white;
    color: #575557;
    border: 2px solid white;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.26);
    transform: translate(50%, 50%);
  }
</style>

<NotificationDisplay />
<main>

  <h1>Cadastro de Tags</h1>
  <FormManager
    type={'NovaTag'}
    onSubmit={data => {
      const values = { ...data.values };
      return createTag(values)
        .then(() => {
          notifier.success('Tag Criada');
          setTimeout(() => hideTag(), 1500);
        })
        .catch(err => {
          notifier.error('Erro!');
        });
      console.log(values);
    }}
    {uuid}
    cancelFn={hideTag}
    addForm={() => {}} />
</main>
