<script>
  import { fly } from "svelte/transition";
  import { listNaturalsWithAccountsValues } from "../queries/list-naturals-with-accounts-values.js";
  import { nodeRetrieve } from "../queries/node-retrieve";
  import { navigateTo } from "svelte-router-spa";
  import { DrillDownStore } from "../store/drilldown-store.js";
  import { onMount } from "svelte";
  let data = [];

  const formatter = new Intl.NumberFormat("pt-BR", {
    style: "currency",
    currency: "BRL"
  });
  const getData = async () => {
    const _data = await listNaturalsWithAccountsValues();
    console.log(_data);
    const dataWithLinks = _data.map(element => {
      const natural = element.natural;

      return {
        ...element,
        title: natural.title
      };
    });
    data = [...dataWithLinks];
    console.log("[data]", data);
  };
  onMount(() => {
    getData();
  });
  const editNatural = naturalUUID => {
    console.log("editNatural", naturalUUID);
    navigateTo(`natural/${naturalUUID}/edit`);
  };
  const backToBalanco = () => {
    navigateTo(`/`);
  };
</script>

<style>
  main {
    border-radius: 7px;
    transition: all 0.2s;
    background-color: white;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.46);
    height: 30rem;
    width: 75rem;
    margin: 1rem 1rem;
    padding: 0.5rem 0.5rem;
    transform: translate(-50%, -50%);
    position: absolute;
    top: 50%;
    left: 50%;
    display: grid;
  }
  .natural-table {
    display: grid;
    grid-auto-flow: row;
    overflow-x: auto;

    transition: all 0.2s;

    margin: 1rem 1rem;
    padding: 0.5rem 0.5rem;
    grid-row-gap: 2rem;
  }
  .title {
    display: grid;
    position: sticky;
    grid-auto-flow: column;
    grid-template-columns: repeat(6, 11.5rem);
  }
  .title > div {
    text-transform: uppercase;
  }

  .journal__accounting {
    display: grid;
    grid-auto-flow: row;
    font-size: 0.55rem;
  }
  .journal__tags {
    display: grid;
    grid-auto-flow: row;
    font-size: 0.55rem;
  }
  .journal__value {
    display: grid;
    grid-auto-flow: row;
    font-size: 0.55rem;
  }
  .journal__persona {
    display: grid;
    grid-auto-flow: row;
    font-size: 0.55rem;
  }
  .natural-table__content {
    cursor: pointer;
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: repeat(6, 11.5rem);
    transition: all 0.3s;
  }
  .natural-table__content:hover {
    color: white;
    background-color: darkslategray;
  }
  button {
    transform: scale(0.75);
  }
  .buttons {
    position: absolute;
    top: 0;
    left: 0;
    transform: translateY(-36%);
  }
</style>

<main>
  <div class="buttons">
    <button on:click={backToBalanco}>VOLTAR</button>
  </div>
  <div class="natural-table">
    <div class="title">
      <div>Title</div>
      <div>Description</div>
      <div>Value</div>
      <div>Accounting</div>
      <div>Tags</div>
      <div>Persona</div>
    </div>
    {#each data as element}
      <div
        class="natural-table__content"
        on:click={() => editNatural(element['natural'].uuid)}>
        <div class="journal__title">{element.title}</div>
        <div class="journal__text">{''}</div>

        <div class="journal__value">
          {#each element['values'].filter(value => !!value) as value}
            <div>
              | Valor:
              <strong>{formatter.format(value.amount)}</strong>
              - Data de Vencimento :
              <strong>{value['Data de vencimento']}</strong>
              - Data de CompetÃªncia :
              <strong>{value['Data de competÃªncia']}</strong>
              - Unidade :
              <strong>{value['unit']}</strong>
              |
            </div>
          {/each}
        </div>
        <div class="journal__accounting">
          {#each element['journals'].filter(journal => !!journal.journal) as journal}
            <div>
              | Valor:
              <strong>{formatter.format(journal.journal.portion)}</strong>
              Conta
              <strong>:{journal.account} -</strong>
              Nat.:
              <strong>{journal.journal.nature} |</strong>
            </div>
          {/each}

        </div>
        <div class="journal__tags">
          {#each element['tags'].filter(tag => !!tag) as tag}
            <div>{tag.tagGroup}: {tag.tagName}</div>
          {/each}
        </div>
        <div class="journal__persona">
          {#each element['personas'].filter(tag => !!tag) as tag}
            <div>
              Persona:
              <strong>{tag.name}</strong>
            </div>
          {/each}
        </div>
      </div>
    {/each}
  </div>
</main>
