<script>
  import { onMount } from "svelte";
  import { tagsList } from "./queries/tags-list.js";

  export let generateReport;
  function uuidv4() {
    return "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(/[xy]/g, function(c) {
      var r = (Math.random() * 16) | 0,
        v = c == "x" ? r : (r & 0x3) | 0x8;
      return v.toString(16);
    });
  }
  let tags = [
    { id: 1, name: "balanÃ§o", key: "journal.balanÃ§o", frontendUUID: uuidv4() },
    { id: 2, name: "grupo", key: "journal.grupo", frontendUUID: uuidv4() },
    { id: 9, name: "rubrica", key: "journal.rubrica", frontendUUID: uuidv4() }
  ];
  let listOftags = [
    { id: 1, name: "balanÃ§o", key: "journal.balanÃ§o" },
    { id: 2, name: "grupo", key: "journal.grupo" },
    { id: 9, name: "rubrica", key: "journal.rubrica" },
    { id: 92, name: "Persona", key: "Persona", tag: true }
  ];

  onMount(() => {
    tagsList()
      .then(data => {
        const _tags = [...new Set(data.map(tag => tag.group))].map(t => ({
          id: uuidv4(),
          name: t,
          key: t,
          tag: true
        }));
        listOftags = [...listOftags, ..._tags];
        console.log("tagsChooser", _tags);
      })
      .catch(err => console.log(err));
  });
  function onChangerValue(event, frontendUUID) {
    let id = event.target.parentNode.id;
    console.log(event.target.name, event.target.value);
    tags = tags.map(entry => {
      if (entry.frontendUUID === frontendUUID) {
        let obj = {};
        obj = listOftags.find(el => el.id == event.target.value);
        return { ...entry, ...obj };
      }
      return { ...entry };
    });
  }
  const addTag = () => {
    tags = [...tags, { tagUUID: null, frontendUUID: uuidv4() }];
  };
  function removeTag(line) {
    tags = tags.filter(entry => entry.frontendUUID != line);
  }
</script>

<style>
  main {
    display: flex;
  }
  .tag {
    display: grid;
    grid-auto-flow: column;
    align-items: baseline;
  }
  select {
    width: 5rem;
    font-size: 0.75rem;
  }
  .remove {
    cursor: pointer;
  }
  button {
    transform: scale(0.75);
  }
</style>

<main>
  {#each tags as tag (tag.frontendUUID)}
    <div class="tag">
      <select
        name="tagUUID"
        on:input={event => onChangerValue(event, tag.frontendUUID)}>
        <option value={'null'} selected={!tag.id}>
          ---- SELECIONE tag ----
        </option>
        {#each listOftags as option (option.id)}
          <option value={option.id} selected={tag.id === option.id}>
            {option.name}
          </option>
        {:else}
          <option>Any</option>
        {/each}
      </select>
      <div class="remove" on:click={() => removeTag(tag.frontendUUID)}>âŒ</div>
    </div>
  {/each}
</main>
<button on:click={addTag}>Adicionar Tag</button>
<button on:click={() => generateReport(tags)}>Fazer mÃ¡gica</button>
