<script>
  import { fly } from "svelte/transition";
  import { journalList } from "../queries/list-journals-2.js";
  import { nodeRetrieve } from "../queries/node-retrieve";
  import { navigateTo } from "svelte-router-spa";
  import { DrillDownStore } from "../store/drilldown-store.js";
  import { onMount } from "svelte";
  let data = [];

  const formatter = new Intl.NumberFormat("pt-BR", {
    style: "currency",
    currency: "BRL"
  });
  const getData = async () => {
    console.log($DrillDownStore);
    const _data = $DrillDownStore.data ? $DrillDownStore.data : [];
    const dataWithLinks = _data.map(element => {
      const natural = element["journal.natural"].contents;
      const title = natural.find(field => field.id === "title")
        ? natural.find(field => field.id === "title").value
        : "";
      const text = natural.find(field => field.id === "text")
        ? natural.find(field => field.id === "text").value
        : "";
      const file = natural.find(field => field.id === "file")
        ? natural.find(field => field.id === "file").value
        : "";
      return {
        ...element,
        title,
        text,
        file
      };
    });
    data = [...dataWithLinks];
    console.log("[data]", data);
  };
  onMount(() => {
    getData();
  });
  const editNatural = naturalUUID => {
    console.log("editNatural", naturalUUID);
    navigateTo(`natural/${naturalUUID}/edit`);
  };
  const backToBalanco = () => {
    navigateTo(`/`);
  };
</script>

<style>
  main {
    border-radius: 7px;
    transition: all 0.2s;
    background-color: white;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.46);
    height: 30rem;
    width: 75rem;
    margin: 1rem 1rem;
    padding: 0.5rem 0.5rem;
    transform: translate(-50%, -50%);
    position: absolute;
    top: 50%;
    left: 50%;
    display: grid;
  }
  .natural-table {
    display: grid;
    grid-auto-flow: row;
    overflow-x: auto;

    transition: all 0.2s;

    margin: 1rem 1rem;
    padding: 0.5rem 0.5rem;
    grid-row-gap: 2rem;
  }
  .title {
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: repeat(7, 8.5rem);
  }
  .title > div {
    text-transform: uppercase;
  }

  .journal__accounting {
    font-size: 0.75rem;
  }
  .journal__tags {
    display: grid;
    grid-auto-flow: row;
    font-size: 0.55rem;
  }
  .journal__persona {
    display: grid;
    grid-auto-flow: row;
    font-size: 0.55rem;
  }
  .natural-table__content {
    cursor: pointer;
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: repeat(7, 8.5rem);
    transition: all 0.3s;
  }
  .natural-table__content:hover {
    color: white;
    background-color: darkslategray;
  }
  button {
    transform: scale(0.75);
  }
  .buttons {
    position: absolute;
    top: 0;
    left: 0;
    transform: translateY(-36%);
  }
  .tagAmount {
    font-size: 0.55rem;
  }
</style>

<main>
  <div class="buttons">
    <button on:click={backToBalanco}>VOLTAR</button>
  </div>
  <div class="natural-table">
    <div class="title">
      <div>Title</div>
      <div>Description</div>
      <div>Docs</div>
      <div>Value</div>
      <div>Accounting</div>
      <div>Tags</div>
      <div>Persona</div>
    </div>
    {#each data as element}
      <div
        class="natural-table__content"
        on:click={() => editNatural(element['journal.natural'].uuid)}>
        <div class="journal__title">{element.title}</div>
        <div class="journal__text">{element.text}</div>
        <div class="journal__file">{element.file}</div>
        <div class="journal__value">
          {formatter.format(element.amount)}
          <span class="tagAmount">
            {element.splitted ? ` de ${formatter.format(element.amountTotal)}` : ''}
          </span>
        </div>
        <div class="journal__accounting">
          {element['journal.balanÃ§o'] + ' | ' + element['journal.grupo'] + ' | ' + element['journal.rubrica']}
        </div>
        <div class="journal__tags">
          {#each element['journal.tags'].filter(tag => !!tag.value && tag.title !== 'Persona') as tag}
            <div>{tag.title}: {tag.value}</div>
          {/each}
        </div>
        <div class="journal__persona">
          {#each element['journal.tags'].filter(tag => !!tag.value && tag.title == 'Persona') as tag}
            <div>{tag.title}: {tag.value}</div>
          {/each}
        </div>
      </div>
    {/each}
  </div>
</main>
