<script>
  import { fade, fly } from "svelte/transition";
  import { onMount } from "svelte";
  import { v4 as uuidv4 } from "uuid";
  import { navigateTo } from "svelte-router-spa";
  import FormManager from "./../FormManager.svelte";
  import NaturalActions from "./NaturalActions.svelte";
  import NaturalOptions from "./NaturalOptions.svelte";
  import { nodeCreate } from "../queries/node-create.js";
  import { deleteNatural } from "../queries/intentions/natural-delete.js";
  import { IntentStore } from "../store/intent-store.js";
  import { actions } from "../actions/actions.js";
  import { naturalFormList } from "../queries/natural-form-list.js";
  import { NotificationDisplay, notifier } from "@beyonk/svelte-notifications";
  let showActions = {};
  let showOptions = {};
  export let currentRoute;

  let uuid = currentRoute.namedParams.id;
  $: console.log("[currentRoute]", currentRoute);
  const hideActions = uuid => (showActions[uuid] = false);
  const createNatural = ({ fields, values, type, parentUUID }) => {
    return nodeCreate(fields, values, type, uuid)
      .then(() => {
        notifier.success("Natural Criado");
      })
      .catch(err => console.log("err created", err));
  };
  const backHome = () => {
    navigateTo("/");
    IntentStore.reset();
  };

  const getForms = async parentUUID => {
    const forms = await naturalFormList(parentUUID);
    const formsWithOnSubmit = forms.map(form => {
      const action = actions.find(action => action.intent === form.type) || {};
      const fn = () => console.log("empty");
      return {
        ...form,
        frontendUUID: form.uuid,
        onSubmit: action.onSubmit || fn,
        onUpdate: action.onUpdate || fn,
        onDelete: action.onDelete || fn
      };
    });

    IntentStore.setForms(formsWithOnSubmit);
    console.log(formsWithOnSubmit);
  };
  onMount(() => {
    getForms(uuid);
  });
  const scrollY = node => {
    const html = node;
    const scrollFn = e => {
      var delta = Math.max(-1, Math.min(1, e.wheelDelta || -e.detail));

      html.scrollLeft -= delta * 80; // Multiplied by 40
      e.preventDefault();
    };
    node.addEventListener("mousewheel", scrollFn, false);
  };
</script>

<style>
  main {
    /* height: 100%;

    display: flex;
    align-items: center;
    padding: 0 2rem; */
    display: grid;
    grid-gap: calc(20px / 2);
    grid-auto-flow: column;
    grid-template-rows: minmax(150px, 1fr);
    align-items: center;
    height: 98%;
    width: 100%;
    overflow-x: scroll;
    scroll-snap-type: x proximity;
    padding-bottom: calc(0.75 * 20px);
    margin-bottom: calc(-0.25 * 20px);
    padding: 0rem 2rem;
  }
  .title {
    color: #c2c5c9;
    position: absolute;
    font-size: 3rem;
    /* padding: 2rem 2rem; */
    top: 2rem;
    left: 2rem;
    z-index: -1;
  }
  .item {
    border-radius: 7px;
    cursor: pointer;
    transition: all 0.2s;
    background-color: white;
    height: 25rem;
    width: 25rem;
    display: grid;
    grid-template-rows: 1.5rem 1fr min-content;
    /* overflow: hidden; */
    grid-template-areas:
      "title"
      "content"
      "footer";
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.46);
    position: relative;
  }
  .item__title {
    border-top-right-radius: 7px;
    border-top-left-radius: 7px;

    grid-area: title;
    color: #4b4b4c;
    display: flex;
    font-weight: 600;
    justify-content: flex-start;
    align-items: center;
    background-color: #f2f2f3;
    font-size: 0.5rem;
    padding: 0 1rem;
  }
  .item__content {
    position: relative;
    margin-top: 1rem;
    padding: 0rem 1rem;
    grid-area: content;
  }
  .item__footer {
    color: #bfbebf;
    grid-area: footer;
    font-size: 0.5rem;
    padding: 0.5rem 1rem;
  }
</style>

<main use:scrollY>
  <NotificationDisplay />

  <div class="item" in:fly={{ x: 200, duration: 700, delay: 600 }}>
    <div
      class="item__title"
      on:click={() => (showOptions[uuid] = !showOptions[uuid])}>
      LanÃ§amento Natural
    </div>
    <div class="item__content">

      <FormManager
        type={'Natural'}
        onSubmit={data => {
          return createNatural(data)
            .then(() => {
              console.log('ok');
            })
            .catch(err => console.log('error', err));
        }}
        {uuid}
        cancelFn={() => backHome()}
        addForm={() => {}} />
    </div>
    <div
      class="item__footer"
      on:click={() => (showActions[uuid] = !showActions[uuid])}>
      âŒ˜ Mais aÃ§Ãµes
    </div>
    {#if showActions[uuid] === true}
      <NaturalActions
        parentUUID={uuid}
        rootUUID={uuid}
        {actions}
        hideActions={() => hideActions(uuid)} />
    {/if}
    {#if showOptions[uuid] === true}
      <NaturalOptions
        {uuid}
        onDelete={uuid => deleteNatural(uuid)
            .then(() => {
              notifier.info(`Deletado`);
              navigateTo('/');
            })
            .catch(err => console.log('erro ao apagar'))} />
    {/if}
  </div>
  {#each $IntentStore.forms as form (form.frontendUUID)}
    <div
      class="item"
      in:fly={{ x: 200, duration: 700, delay: 600 }}
      out:fly={{ x: 200, duration: 700 }}>
      <div
        class="item__title"
        on:click={() => (showOptions[form.frontendUUID] = !showOptions[form.frontendUUID])}>
        {form.title}
      </div>
      <div class="item__content">

        <FormManager
          type={form.type}
          onUpdate={({ fields, values, type, parentUUID }) => {
            return form
              .onUpdate({
                fields,
                values,
                type,
                parentUUID,
                frontendUUID: form.frontendUUID
              })
              .then(res => {
                console.log('ok', res);
                notifier.success(`${form.title} Atualizado`);
              });
          }}
          onSubmit={({ fields, values, type, parentUUID }) => {
            return form
              .onSubmit({
                fields,
                values,
                type,
                parentUUID,
                frontendUUID: form.frontendUUID
              })
              .then(res => {
                console.log('ok', res);
                notifier.success(`${form.title} executado`);
              })
              .catch(err => notifier.danger(`Erro:${form.title}`));
          }}
          parentUUID={form.parentUUID ? form.parentUUID : uuid}
          uuid={form.frontendUUID}
          cancelFn={() => IntentStore.removeForm(form.frontendUUID)}
          addForm={() => {}} />
      </div>
      <div
        class="item__footer"
        on:click={() => (showActions[form.frontendUUID] = !showActions[form.frontendUUID])}>
        âŒ˜ Mais aÃ§Ãµes
      </div>
      {#if showActions[form.frontendUUID] === true}
        <NaturalActions
          parentUUID={form.frontendUUID}
          {actions}
          rootUUID={uuid}
          hideActions={() => hideActions(form.frontendUUID)} />
      {/if}
      {#if showOptions[form.frontendUUID] === true}
        <NaturalOptions
          uuid={form.frontendUUID}
          onDelete={uuid => form
              .onDelete(uuid)
              .then(() => {
                IntentStore.removeForm(form.frontendUUID);
                notifier.info(`Deletado`);
              })
              .catch(err => console.log('erro ao apagar'))} />
      {/if}
    </div>
  {/each}
</main>
