<script>
  import { createEventDispatcher } from "svelte";
  import clsx from "clsx";

  // Declar variables.
  export let id = "";
  export let name = "";
  export let classe = "";
  export let disabled = null;
  export let journals = [];
  export let groups = [];
  export let value = [];
  const dispatch = createEventDispatcher();
  function uuidv4() {
    return "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(/[xy]/g, function(c) {
      var r = (Math.random() * 16) | 0,
        v = c == "x" ? r : (r & 0x3) | 0x8;
      return v.toString(16);
    });
  }
  let tags = [];

  // Change value field.
  const addTag = () => {
    tags = [...tags, { tagUUID: null, frontendUUID: uuidv4() }];
  };
  function onChangerValue(event, frontendUUID) {
    let id = event.target.parentNode.id;
    tags = tags.map(entry => {
      if (entry.frontendUUID === frontendUUID) {
        let obj = {};
        obj[event.target.name] = event.target.value;
        return { ...entry, ...obj };
      }
      return { ...entry };
    });

    dispatch("changeValue", {
      name: name,
      value: tags
    });
  }

  // Delete entry.
  function deleteEntry(line) {
    tags = tags.filter(entry => entry.frontendUUID != line);
    dispatch("changeValue", {
      name: name,
      value: tags
    });
  }
  $: tags = [...value];
  $: console.log("[tags]", groups);
  $: console.log("[tagGroup", [...new Set(groups.map(tag => tag.group))]);
</script>

<style>
  .tag {
    display: grid;
    grid-auto-flow: row;
    grid-template-columns: 4fr 1fr 1fr;
    justify-content: flex-start;
  }
  select,
  input {
    font-size: 0.5rem;
    height: 2rem;
    width: 100%;
  }
  button {
    font-size: 0.5rem;
  }
  main {
    height: 15rem;
    overflow: auto;
  }
</style>

<main>

  {#each tags as tag, i (tag.frontendUUID)}
    <div id={i} name="tagwrapper" class="tag">

      <select
        id={`grupo${i}`}
        name="tagUUID"
        on:input={event => onChangerValue(event, tag.frontendUUID)}>
        <option>Selecionar TAG</option>
        {#each [...new Set(groups.map(tag => tag.group))] as tagGroup}
          <optgroup label={tagGroup}>
            {#each groups.filter(tag => tag.group === tagGroup) as group (group.value + group.title + tagGroup)}
              <option
                value={group.value}
                selected={tag.tagUUID === group.value}>
                {group.title}
              </option>
            {/each}
          </optgroup>
        {/each}

      </select>
      <input
        name="amount"
        type="number"
        value={tag.amount}
        on:input={event => onChangerValue(event, tag.frontendUUID)} />
      <button
        type="button"
        on:click|preventDefault={() => {
          deleteEntry(tag.frontendUUID);
        }}>
        ðŸ—‘
      </button>
    </div>
  {/each}

  <button on:click={addTag}>Adicionar Tag</button>
</main>
