<script>
  import { onMount } from "svelte";
  import { fade, fly } from "svelte/transition";
  import BalancoGroup from "./BalancoGroup.svelte";
  import { navigateTo } from "svelte-router-spa";
  import { v4 as uuidv4 } from "uuid";
  import Tree from "svelte-tree";
  import _ from "lodash";
  import { flip } from "svelte/animate";
  import { dndzone } from "svelte-dnd-action";
  import { journalList } from "./queries/list-journals-2.js";
  import { dataMockup } from "./queries/mockup-neo4j.js";
  import { template } from "./queries/mockup-template.js";
  import BalancoOptions from "./BalancoOptions.svelte";
  import BalancoChooser from "./BalancoChooser.svelte";
  import BalancoCellAmount from "./BalancoCellAmount.svelte";
  import { contentGridStore } from "./store/store.js";
  import { DrillDownStore } from "./store/drilldown-store.js";
  import moment from "moment";
  import arrayToTree from "array-to-tree";
  import TagsChooser from "./TagsChooser.svelte";
  import { splitJournals } from "./split-journals.js";
  import { setContext } from "svelte";
  const setDrillDownData = data => {
    console.log("setting....", data);
    DrillDownStore.setData(data);
    setContext("drillDownData", data);
  };
  let tree = [];
  const formatter = new Intl.NumberFormat("pt-BR", {
    style: "currency",
    currency: "BRl"
  });
  const eraseContent = () => {
    while ($contentGridStore.hasChildNodes()) {
      const lastChild = $contentGridStore.lastChild;
      console.log($contentGridStore, "lastChild", lastChild);
      lastChild.remove();
    }
  };
  const addContent = () => {
    console.log($contentGridStore);
    const cell = document.createElement("span");
    cell.innerText = "teste";
    $contentGridStore.append(cell);
  };
  function removeAllChildNodes(parent) {}
  let flattenGroups = [];
  let contentGrid = null;
  let groupsHTML = null;
  let selectedDate = -1;
  let dateGrid = null;
  let itemsChooser = [
    { id: 1, name: "balanÃ§o", key: "journal.`balanÃ§o`" },
    { id: 2, name: "grupo", key: "journal.`grupo`" },
    { id: 9, name: "rubrica", key: "journal.`rubrica`" },
    { id: 92, name: "personaName", key: "journal.`personaName`" },
    {
      id: 3,
      name: "Natureza do projeto",
      key: "journal.`Natureza do projeto`"
    },
    { id: 4, name: "TÃ­tulo", key: "TÃ­tulo", key: "journal.`TÃ­tulo`" },
    { id: 5, name: "Tipo", key: "journal.`Tipo`" },
    { id: 7, name: "Parceria", key: "journal.`Parceria`" }
  ];
  let itemsOptions = [];

  let accounts = [];
  let headers = [2019, 2020];

  let dates = [{ year: "2020", ref: null }, { year: "2019", ref: null }];
  const months = [
    "Jan",
    "Fev",
    "Mar",
    "Maio",
    "Jun",
    "Jul",
    "Ago",
    "Set",
    "Out",
    "Nov",
    "Dec"
  ];
  let groups = [];
  let timePeriod = "year";
  onMount(() => {
    contentGridStore.set(contentGrid);
    accounts = [];
    tree = [];
    showBalance();
    timePeriod = "year";
  });

  const changeOptions = e => {
    flattenGroups = [];
    console.log("MUDOU OPÃ‡Ã•ES", e.detail);
    removeAllChildNodes(groupsHTML);
    itemsOptions = [...e.detail];
    journalList(itemsOptions)
      .then(data => {
        accounts = [...data];

        setGroups(itemsOptions);
      })
      .catch(err => console.log(err));
  };
  const setGroups = itemsOptions => {
    let groupsAux = [];
    let _groupsAux = [];
    _groupsAux = [
      ..._groupsAux,
      {
        date: true,
        key: "data",
        id: "data",
        name: "data",
        order: -1,
        parent_id: null
      }
    ];
    let itemOptionsWithValues = itemsOptions.map((option, i) => {
      let distinctValues = [
        ...new Set(accounts.map(account => account[option.key]))
      ];
      return { name: option.name, key: option.key, values: distinctValues };
    });
    itemOptionsWithValues.forEach((option, i) => {
      if (i == 0) {
        option.values.forEach((value, j) => {
          _groupsAux = [
            ..._groupsAux,
            {
              header: true,
              key: option.key,
              id: value,
              name: value,
              parent_id: null
            }
          ];
        });
      }
      if (itemsOptions[i + 1]) {
        option.values.forEach(value => {
          let childs = accounts.filter(account => account[option.key] == value);
          childs.forEach(child => {
            _groupsAux = [
              ..._groupsAux,
              {
                amount: child.amount ? formatter.format(child.amount) : "-",
                key: itemsOptions[i + 1].name,
                id: child[`${itemsOptions[i + 1].key}`]
                  ? child[`${itemsOptions[i + 1].key}`]
                  : "---",
                name: child[`${itemsOptions[i + 1].key}`]
                  ? child[`${itemsOptions[i + 1].key}`]
                  : "---",
                parent_id: value
              }
            ];
          });
        });
      }
    });
    _groupsAux = _.uniqBy(_groupsAux, e => e.id + e.parent_id);

    flattenGroups = [..._groupsAux];
    if (contentGrid) {
      contentGrid.style[
        "grid-template-rows"
      ] = `repeat(${flattenGroups.length},1fr)`;
    }

    _groupsAux = _groupsAux.map(element => {
      const order = template.find(t => t.name === element.name)
        ? template.find(t => t.name === element.name).order
        : null;
      return { ...element, order };
    });
    _groupsAux = _.orderBy(_groupsAux, ["order"], ["asc"]);
    groups = [...arrayToTree(_groupsAux)];
    tree = [...arrayToTree(_groupsAux)];
    tree = [...tree];
    console.log("tree", tree, "groupsAux", _groupsAux);
  };

  const deepthArray = deepth => {};
  const setCellPosition = (node, pos) => {
    node.style["grid-column"] = `${pos.y + 1}`;
    node.style["grid-row"] = `${pos.x + 1}`;
  };
  const createAmountCell = event => {
    console.log(event.detail);
  };

  const setPositionMonth = (node, pos) => {
    node.style["grid-column"] = `${pos.y + 1}`;
    node.style["grid-row"] = `${pos.x + 1}`;
  };
  const setPositionYear = (node, data) => {
    if (data.index < data.selectedDate) {
      node.style["grid-column"] = `${data.index + 1}`;
    }
    if (data.index > data.selectedDate) {
      node.style["grid-column"] = `${data.index - 1 + 12}`;
    }
    if (data.index === data.selectedDate) {
      node.style["grid-column"] = `${data.index + 1}`;
    }
  };
  const setPath = (node, n2) => {
    const parentNode =
      node.parentNode.parentNode.parentNode.parentNode.firstChild.firstChild
        .nextElementSibling;

    if (parentNode) {
      const oldPath = parentNode.getAttribute("path")
        ? parentNode.getAttribute("path")
        : null;
      node.setAttribute(
        "path",
        oldPath ? oldPath + "|" + n2.name : n2.name ? n2.name : "null"
      );
    } else {
      node.setAttribute("path", n2.name ? n2.name : "null");
    }
    let path = node.getAttribute("path").split("|");
    console.log("accounts", accounts);
    console.log("path", path);
    let matchOBJ = {};
    path.forEach((value, i) => {
      if (itemsOptions[i] && value) {
        matchOBJ = {
          ...matchOBJ,
          [itemsOptions[i].key]: value === "---" ? null : value
        };
      }
      if (!value) {
        console.log(itemsOptions[i].key, value);
      }
    });
    console.log("matchObj", matchOBJ);
    const dataF = _.filter(accounts, _.matches({ ...matchOBJ }));

    if (timePeriod === "year") {
      const years = [...new Set(accounts.map(el => el.year))];
      const dateGrid = node.firstChild.firstChild.nextElementSibling;
      dateGrid.style[
        "grid-template-columns"
      ] = `repeat(${years.length},4.55rem)`;

      years.forEach(year => {
        const data = _.filter(
          dataF,
          _.matches({
            year: year
          })
        );
        const amount = _.sumBy(
          _.filter(dataF, _.matches({ year: year })),
          "amount"
        );
       
        const cell = document.createElement("div");

        if (amount > 0 && !n2.date) {
          cell.style["color"] = `green`;
        }
        if (amount < 0 && !n2.date) {
          cell.style["color"] = `red`;
        }
        if (n2.header && !n2.date) {
          cell.style["color"] = `blue`;
        }
        if (n2.date) {
          cell.innerText = year;
          cell.style["color"] = `#008B8B`;
        } else {
          cell.innerText = formatter.format(amount);
        }
         console.log("year,path,amount", year, matchOBJ, amount,formatter.format(amount));
        cell.addEventListener("click", e => {
          e.preventDefault();
          setDrillDownData(data);
          navigateTo(`drilldown/11`);
        });
        dateGrid.append(cell);
      });
    }
    if (timePeriod === "month") {
      const selectedYear = "2020";
      const months = [
        "JAN",
        "FEV",
        "MAR",
        "ABR",
        "MAIO",
        "JUN",
        "JUL",
        "AGO",
        "SET",
        "OUT",
        "NOV",
        "DEZ"
      ];
      const dateGrid = node.firstChild.firstChild.nextElementSibling;
      dateGrid.style["grid-template-columns"] = "repeat(12,4.55rem)";

      months.forEach((month, i) => {
        const data = _.filter(
          dataF,
          _.matches({
            year: selectedYear,
            month: `${i < 9 ? "0" + (i + 1) : i + 1}`
          })
        );
        const amount = _.sumBy(data, "amount");

        const cell = document.createElement("div");

        if (amount > 0) {
          cell.style["color"] = `green`;
        }
        if (amount < 0) {
          cell.style["color"] = `red`;
        }
        if (n2.header) {
          cell.style["color"] = `blue`;
        }
        if (n2.date) {
          cell.innerText = months[i] + "/" + selectedYear;

          cell.style["color"] = `#008B8B`;
        } else {
          cell.innerText = formatter.format(amount);
        }
        cell.addEventListener("click", e => {
          e.preventDefault();
          setDrillDownData(data);
          navigateTo(`drilldown/11`);
        });
        dateGrid.append(cell);
      });
    }
  };
  const showBalance = async () => {
    const tags = [
      { id: 1, name: "balanÃ§o", key: "journal.balanÃ§o" },
      { id: 2, name: "grupo", key: "journal.grupo" },
      { id: 9, name: "rubrica", key: "journal.rubrica" }
    ];

    itemsOptions = [...tags];
    accounts = await journalList();
    accounts = [...accounts];
    console.log("[accounts]", accounts);

    setGroups(itemsOptions);
  };
  const addTimePeriod = (node, data) => {};
  const generateReport = async tags => {
    accounts = [];
    tree = [];
    console.log(tags);
    itemsOptions = [...tags];
    accounts = [...(await journalList())];
    accounts = [...splitJournals(accounts, tags)];
    setGroups(itemsOptions);
    console.log(splitJournals(accounts, tags));
  };
  const createNatural = () => {
    console.log("clicked");
    navigateTo(`natural/${uuidv4()}`);
  };
</script>

<style>
  .grid {
    overflow: auto;
  }
  .date {
    grid-area: date;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-auto-flow: column;
  }
  .group {
    align-self: flex-start;
    grid-area: group;
    display: grid;
    grid-auto-flow: row;
  }

  .date-grid {
    display: grid;
    grid-auto-flow: column;
    justify-content: end;
    justify-items: end;
    margin-left: 2.5rem;
  }
  .title-tree {
    display: grid;
    grid-auto-flow: column;

    width: 100%;
  }
  .name {
    font-size: 0.75rem;
    width: 100%;
  }
  .color {
    color: blue !important;
  }
  main {
    border-radius: 7px;
    transition: all 0.2s;
    background-color: white;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.46);
    height: 30rem;
    width: 55rem;
    margin: 1rem 1rem;
    padding: 0.5rem 0.5rem;
    transform: translate(-50%, -50%);
    position: absolute;
    top: 50%;
    left: 50%;
    display: grid;
    grid-template-rows: 1rem 24rem 1rem;
  }
  .menu__bottom {
  }
  .menu__top {
    transform: translateY(-145%);
  }
  button {
    transform: scale(0.75);
  }
</style>

<main>
  <div class="menu__top">
    <button
      on:click={() => {
        accounts = [];
        tree = [];
        showBalance();
        timePeriod = 'year';
      }}>
      Ver BalanÃ§o | Ano
    </button>
    <button
      on:click={() => {
        accounts = [];
        tree = [];
        showBalance();
        timePeriod = 'month';
      }}>
      Ver BalanÃ§o | MÃªs
    </button>
    <button on:click={createNatural}>Novo Natural</button>
  </div>
  <div class="grid">

    <div class="group" bind:this={groupsHTML}>
      <Tree {tree} let:node>
        <div
          class="name"
          name={node.name}
          use:setPath={node}
          class:color={node.header}>

          <div class="title-tree">
            <span>{node.date ? '--' : node.name}</span>
            <div class="date-grid" />
          </div>

        </div>
      </Tree>
    </div>

  </div>
  <div class="menu__bottom">
    <TagsChooser {generateReport} />
  </div>
</main>
