<script>
  import { createEventDispatcher, onMount } from "svelte";
  import { textAreaResize } from "./autoresize_textarea";

  import clsx from "clsx";
  // Declar variables.
  export let id = "";
  export let name = "";
  export let value = "";
  export let classe = "";
  export let rows = "";
  export let cols = 50;
  export let required = false;
  export let disabled = false;
  export let readonly = false;
  const dispatch = createEventDispatcher();
  // Change value.
  function onChangerValue(event) {
    let value = event.target.value;
    let valueWithoutBreak = value.replace("\n", " ");
    dispatch("changeValue", {
      name: name,
      value: value
    });
  }
  // Insert default value.
  onMount(() => {
    dispatch("changeValue", {
      name,
      value
    });
  });
</script>

<style>
  textarea {
    border-radius: 7px;
    width: 23rem;
    height: 10rem;
    max-height: 10rem;
    font-weight: 600;
    overflow: auto;
    margin: 0px 0px 8px;
    resize: none;
  }
</style>

<textarea
  {id}
  {name}
  class={clsx(classe)}
  {required}
  {disabled}
  {readonly}
  {rows}
  {cols}
  on:input={onChangerValue}>
  {value}
</textarea>
