<script>
  import _ from "lodash";
  import { setContext, getContext, onMount } from "svelte";
  import { beforeUpdate, afterUpdate } from "svelte";

  export let group;

  export let data = [];
  export let index = 0;
  let walker = 0;

  const formatter = new Intl.NumberFormat("pt-BR", {
    style: "currency",
    currency: "BRl"
  });

  const hasArray = () => group.some(g => _.isArray(g));

  const setCells = node => {
    node.style["grid-column"] = `${group.year == "2020" ? 1 : 2}`;
    node.style["grid-row"] = `${index + 1}`;
  };
</script>

<style>
  span {
    font-size: 0.65rem;
  }
</style>

{#if group.amount}
  <div use:setCells={group}>
    <span>{formatter.format(group.amount)}({walker})</span>
  </div>
{:else}
  <div use:setCells={group}>
    <span>{formatter.format(0)}</span>
  </div>
{/if}
{#if _.isArray(group.children)}
  {#each group.children as g}
    <svelte:self group={g} index={index + 1} {data} />
  {/each}
{/if}
