<script>
  import { createEventDispatcher } from "svelte";
  import clsx from "clsx";
  // Declar variables.
  export let id = "";
  export let name = "";
  export let classe = "";
  export let disabled = null;
  export let personas = [];
  export let listOfPersonas = [];
  const dispatch = createEventDispatcher();
  function uuidv4() {
    return "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(/[xy]/g, function(c) {
      var r = (Math.random() * 16) | 0,
        v = c == "x" ? r : (r & 0x3) | 0x8;
      return v.toString(16);
    });
  }
  let entries = [];

  // Change value field.
  const addPersonaEntry = () => {
    entries = [
      ...entries,
      { amount: 0, personaUUID: null, frontendUUID: uuidv4() }
    ];
  };
  function onChangerValue(event, frontendUUID) {
    let id = event.target.parentNode.id;
    console.log(event.target.name, event.target.value);
    entries = entries.map(entry => {
      if (entry.frontendUUID === frontendUUID) {
        let obj = {};
        obj[event.target.name] = event.target.value;
        return { ...entry, ...obj };
      }
      return { ...entry };
    });
    console.log(entries);
    dispatch("changeValue", {
      name: name,
      value: entries
    });
  }

  // Delete entry.
  function deleteEntry(line) {
    entries = entries.filter(entry => entry.frontendUUID != line);
    dispatch("changeValue", {
      name: name,
      value: entries
    });
  }
  $: entries = [...personas];
  $: console.log("[persona]", personas);
</script>

<style>
  .persona {
    display: grid;
    grid-template-columns: repeat(3, min-content);
    justify-content: end;
  }
  input {
    font-size: 0.5rem;
    height: 2rem;
  }
  select {
    font-size: 0.5rem;
    height: 2rem;
  }
  button {
    font-size: 0.5rem;
  }
  .journals {
    height: 15rem;
    overflow: auto;
  }
</style>

<div class="journals">
  {#each entries as persona, i (persona.frontendUUID)}
    <div id={i} name="persona" class="persona">
      <select
        name="personaUUID"
        on:input={event => onChangerValue(event, persona.frontendUUID)}>
        <option value={'null'} selected={!persona.personaUUID}>
          ---- SELECIONE PERSONA ----
        </option>
        {#each listOfPersonas as option (option.name)}
          <option
            value={option.uuid}
            selected={persona.personaUUID === option.uuid}>
            {option.name}
          </option>
        {:else}
          <option>Any</option>
        {/each}
      </select>
      <select
        name="tipo"
        on:input={event => onChangerValue(event, persona.frontendUUID)}>
        <option value={'VALUE_TO'} selected={!persona.tipo}>
          ----- SELECIONAR TIPO -----
        </option>
        <option value={'VALUE_FROM'} selected={persona.tipo === 'VALUE_FROM'}>
          VALUE_FROM
        </option>
        <option value={'VALUE_TO'} selected={persona.tipo === 'VALUE_TO'}>
          VALUE_TO
        </option>
      </select>
      <input
        name="amount"
        type="number"
        value={persona.amount}
        on:input={event => onChangerValue(event, persona.frontendUUID)} />
      <button
        type="button"
        on:click|preventDefault={() => {
          deleteEntry(persona.frontendUUID);
        }}>
        Remove
      </button>
    </div>
  {/each}
  <button on:click={addPersonaEntry}>Adicionar Mais Um</button>
</div>
