<script>
  import { createEventDispatcher } from "svelte";
  import clsx from "clsx";
  // Declar variables.
  export let id = "";
  export let name = "";
  export let classe = "";
  export let disabled = null;
  export let multiple = false;
  export let showPreview = false;
  export let files = [];
  $: console.log("files", files);
  const dispatch = createEventDispatcher();
  // Change value field.
  function onChangerValue(event) {
    let newFiles = Array.from(event.target.files);
    dispatch("changeValue", {
      name: name,
      value: [...files, ...newFiles]
    });
    files = [...files, ...newFiles];
  }

  // Delete file.
  function deleteFile(file) {
    files = files.filter(i => i.name != file.name);
    dispatch("changeValue", {
      name: name,
      value: files
    });
  }
  const downloadFile = file => {
    var link = document.createElement("a");
    link.download = file.name;
    link.href = file;
    link.click();
  };
</script>

<style>
  input {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
  }
  .btn--attach {
    width: 5rem;
    font-weight: 600;
    padding: 0.5rem 1rem;
    cursor: pointer;
    color: #737273;
    background-color: #e3e2e3;
    outline: none;
    border: none;
    border-radius: 12px;
  }
  .infos {
    display: flex;
    font-size: 0.5rem;
  }
  main {
    display: flex;
    justify-content: space-between;
  }
  .list-files {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
  }
  .button {
    margin-left: 0.3rem;
  }
</style>

<main>
  <label class="btn--attach" for={id}>Anexar...</label>
  <input
    type="file"
    {id}
    {name}
    class={clsx(classe)}
    {disabled}
    {multiple}
    on:input={onChangerValue} />

  {#if showPreview}
    {#if files}
      <div class="list-files">
        {#each files as file, i}
          <div class="file">
            <div class="infos">
              <div on:click={() => downloadFile(file)}>{file.name}</div>
              <div
                class="button"
                on:click|preventDefault={() => {
                  deleteFile(file);
                }}>
                âœ–
              </div>
            </div>
          </div>
        {/each}
      </div>
    {/if}
  {/if}
</main>
