<script>
  import { formRetrieve } from "./queries/form-retrieve.js";

  import { get } from "svelte/store";
  import { valuesForm, Field } from "svelte-formly";
  import { accountList } from "./queries/account-list.js";
  import { personaList } from "./queries/persona-list.js";
  import { tagsList } from "./queries/tags-list.js";
  import { storage } from "./firebase/firebase.js";
  import { onMount } from "svelte";
  import { v4 as uuidv4 } from "uuid";
  import { navigateTo } from "svelte-router-spa";
  import { fly } from "svelte/transition";
  import { nodeRetrieve } from "./queries/node-retrieve";

  export let onSubmit;
  export let parentUUID;
  export let uuid;
  export let cancelFn;
  export let type;
  export let onUpdate;
  let storageRef = storage.ref();
  let values = {};
  let retrieved = null;
  let fields = null;
  let listOfPersonas = [];
  let accounts = [];
  let groups = [];
  const blobFromURL = async url => {
    let blob = await fetch(url, { mode: "cors" }).then(r => {
      return r.blob();
    });
    return blob;
  };
  const retrieveNode = async id => {
    retrieved = await nodeRetrieve(id);
    if (retrieved) {
      fields = retrieved.contents;

      fields = await Promise.all(
        fields.map(async field => {
          if (field.type === "file" && field.value.length > 0) {
            let files = await Promise.all(
              field.value.map(async file => {
                const blob = await blobFromURL(file.url);
                console.log(file);
                const newFile = new File([blob], file.filename);
                newFile.url = file.url;
                return newFile;
              })
            );
            return { ...field, value: [...files] };
          }
          return field;
        })
      );
    }
  };
  onMount(() => {
    if (uuid) {
      retrieveNode(uuid);
    }
    tagsList()
      .then(data => (groups = [...data].filter(acc => !!acc.value)))
      .catch(err => console.log(err));
    accountList()
      .then(data => (accounts = [...data].filter(acc => !!acc.value)))
      .catch(err => console.log(err));
    personaList()
      .then(data => (listOfPersonas = [...data]))
      .catch(err => console.log(err));
  });

  const saveToFirestore = async file => {
    const filePath = `/teste-neo4j-mongo/sistema-contabil/${file.name}`;
    const uploadTask = await storageRef.child(filePath).put(file, {
      //customMetadata: {
      // lancamento: uuidLancamento
      // }
    });
    const url = await storageRef.child(filePath).getDownloadURL();
    return url;
  };
  const handleFiles = async form => {
    let naturalFormatted = form.fields.map(async data => {
      if (data.type === "file" && data.value.length > 0) {
        let files = data.value.map(async file => {
          return {
            filename: file.name,
            url: file.url ? file.url : await saveToFirestore(file)
          };
        });
        files = await Promise.all(files);
        return {
          ...data,
          value: [...files]
        };
      }
      return { ...data };
    });
    naturalFormatted = await Promise.all(naturalFormatted);
    return naturalFormatted;
  };
  const onSubmitForm = async form => {
    const data = get(valuesForm);

    if (data && data[uuid] && data[uuid].isValidForm) {
      values = data[uuid].values;
    }

    let fields = await handleFiles(form);
    //   createNode(naturalFormatted, values, form.type);
    onSubmit({ fields, values, type: form.type, parentUUID })
      .then(async () => {
        retrieved = await nodeRetrieve(uuid);
      })
      .catch(err => console.log("error onSubmit"));
  };

  import { nodeUpdate } from "./queries/node-update";
  const updateNode = async (fields, values, oldUUID) => {
    console.log(fields, values, oldUUID);
    nodeUpdate(fields, values, uuid)
      .then(() => console.log("updated"))
      .catch(err => console.log("erro ao update", err));
  };
  const onUpdateForm = async () => {
    const data = get(valuesForm);
    if (data && data[uuid] && data[uuid].isValidForm) {
      values = data[uuid].values;
    }
    let naturalFormatted = fields.map(async data => {
      if (data.type === "file" && data.value.length > 0) {
        let files = data.value.map(async file => {
          return { filename: file.name, url: await saveToFirestore(file) };
        });
        files = await Promise.all(files);
        return {
          ...data,
          value: [...files]
        };
      }
      return { ...data };
    });
    naturalFormatted = await Promise.all(naturalFormatted);
    console.log(naturalFormatted);
    console.log(values);
    if (onUpdate) {
      onUpdate({
        fields: naturalFormatted,
        values,
        uuid,
        parentUUID,
        type
      });
    } else {
      updateNode(naturalFormatted, values, uuid);
    }
  };
  //LOGs
  $: console.log("[retrieved]", retrieved);
  $: console.log("[groups]", groups);
</script>

<style>
  hr {
    background-color: #f4f3f4;
    height: 1px;
    border: 0;
    margin-top: 1rem;
    margin-bottom: 0.7rem;
  }
  button {
    margin-right: 0.5rem;
  }
  form {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }
  .fields {
    flex: auto;
  }
  .wrapper {
    width: 100%;
    height: 100%;
  }
</style>

<div class="wrapper">
  {#await formRetrieve(type)}
    Carregando Formulario
  {:then response}

    <form
      in:fly={{ y: -20, duration: 300, delay: 0 }}
      on:submit|preventDefault={() => {}}>
      <div class="fields">
        {#if fields}
          <Field {fields} {uuid} {accounts} {groups} {listOfPersonas} />
        {:else}
          <Field
            fields={response.fields}
            {uuid}
            {accounts}
            {listOfPersonas}
            {groups} />
        {/if}
      </div>
      <hr />
      <div>
        <button
          class="btn btn-primary"
          type="submit"
          on:click={() => {
            !!retrieved ? onUpdateForm() : onSubmitForm(response);
          }}>
          {!!retrieved == true ? 'Atualizar' : 'Salvar'}
        </button>
        <button on:click={cancelFn}>Cancelar</button>
      </div>
    </form>

  {/await}
</div>
