<script>
  import { fade, fly } from "svelte/transition";
  import { IntentStore } from "../store/intent-store.js";

  import { v4 as uuidv4 } from "uuid";
  export let hideActions;
  export let actions;
  export let parentUUID;
  export let rootUUID;
  //{ text: "Atribuir Valor", endNode: ["Value"], relationship: "VALUE_TO" }

  IntentStore.subscribe(data => console.log("[intentStore]", data));
  const addForm = action => {
    IntentStore.addForm({
      type: action.intent,
      title: action.title,
      onSubmit: action.onSubmit,
      onDelete: action.onDelete,
      onUpdate: action.onUpdate,
      parentUUID: parentUUID,
      frontendUUID: uuidv4(),
      rootUUID: rootUUID
    });
    hideActions();
  };
</script>

<style>
  .main {
    z-index: 9999999;
    font-size: 0.7rem;
    font-weight: 600;
    height: 15rem;
    width: 10rem;
    background-color: #eeedef;
    position: absolute;
    bottom: 1rem;
    right: -7rem;
    border-radius: 10px;
    border-color: white;
    color: #575557;
    border: 2px solid white;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.26);
  }
  hr {
    background-color: rgba(196, 196, 196, 0.5);
    height: 1px;
    border: 0;
    width: 100%;
  }
  .grid {
    display: grid;
    padding-top: 0.5rem;
    justify-items: center;
  }
  .menu__item {
    display: flex;
    flex-direction: column;
    width: 100%;
    /* justify-content: center; */
    align-items: center;
  }
  .menu__item:hover {
  }
</style>

<div
  class="main"
  in:fly={{ y: 20, opacity: 0.2, duration: 300 }}
  out:fly={{ y: -20, opacity: 0.2, duration: 300 }}>
  <div class="grid">
    {#each actions as action (action.id)}
      <div class="menu__item">
        <div on:click={() => addForm({ ...action })}>{action.text}</div>
        <hr />
      </div>
    {/each}
  </div>
</div>
