<script>
  import { onMount } from "svelte";
  import { fade, fly } from "svelte/transition";
  import { naturalList } from "./queries/natural-list.js";
  import { nodeCreate } from "./queries/node-create.js";
  import { v4 as uuidv4 } from "uuid";
  import { navigateTo } from "svelte-router-spa";
  import moment from "moment";
  let naturals = [];
  onMount(() => {
    naturalList()
      .then(data => (naturals = [...data]))
      .catch(err => console.log(err));
  });
  // DEBUG

  $: console.log("[naturals]", naturals);
  const createNatural = () => {
    console.log("clicked");
    navigateTo(`natural/${uuidv4()}`);
  };
  const editNatural = naturalUUID => {
    console.log("editNatural", naturalUUID);
    navigateTo(`natural/${naturalUUID}/edit`);
  };
  const scrollY = node => {
    const html = node;
    const scrollFn = e => {
      var delta = Math.max(-1, Math.min(1, e.wheelDelta || -e.detail));

      html.scrollLeft -= delta * 80; // Multiplied by 40
      e.preventDefault();
    };
    node.addEventListener("mousewheel", scrollFn, false);
  };
</script>

<style>
  main {
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.26);
    display: flex;
    position: relative;
    justify-content: center;
    align-items: center;
    height: 100%;
    /* height: 80vh; */
    padding: 4rem 2rem;
    overflow-x: scroll;
    scroll-snap-type: x proximity;
  }
  .grid {
    display: grid;
    grid-template-columns: 1fr;

    width: 100%;
    grid-gap: 2rem;
    grid-template-columns: repeat(9, 10rem);
  }

  .item {
    border-radius: 7px;
    cursor: pointer;
    transition: all 0.2s;
    background-color: white;
    height: 10rem;
    width: 10rem;
    display: grid;
    grid-template-rows: 1.5rem 1fr min-content;
    overflow: hidden;
    grid-template-areas:
      "title"
      "content"
      "lastchanged";
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.46);
    position: relative;
  }
  .item:hover {
    transform: translateY(-5px);
  }
  .title {
    color: #c2c5c9;
    position: absolute;
    font-size: 3rem;
    /* padding: 2rem 2rem; */
    top: 2rem;
    left: 2rem;
    z-index: -1;
  }
  .item__content {
    grid-area: content;
    color: #4b4b4c;
    padding: 0.5rem 1rem;
    font-size: 1rem;
    font-weight: 600;
  }
  .item__title {
    grid-area: title;
    color: #4b4b4c;
    display: flex;
    font-weight: 600;
    justify-content: flex-start;
    align-items: center;
    background-color: #f2f2f3;
    font-size: 0.5rem;
    padding: 0 1rem;
  }
  .item__lastchanged {
    color: #bfbebf;
    grid-area: lastchanged;
    font-size: 0.5rem;
    padding: 0.5rem 1rem;
  }
  .btn--add {
    cursor: pointer;

    transform: translate(-50%, -50%);
    justify-content: center;
    display: flex;
    background-color: lightsalmon;
    height: 5rem;
    width: 5rem;
    border-radius: 50%;
    align-items: center;
    font-size: 5rem;
    color: white;
    box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.95);
    transition: all 0.5s;
  }
  .btn--add:hover {
    transform: translate(-50%, -50%) scale(1.05);
  }
</style>

<main use:scrollY>
  <div
    class="title"
    in:fly={{ x: 200, duration: 300 }}
    out:fly={{ x: -200, duration: 300 }}>
    Lista de Naturals
  </div>
  <div class="grid">
    {#each naturals as natural (natural.uuid)}
      <div
        class="item"
        in:fly={{ x: 200, duration: 300, delay: 150 }}
        on:click={() => editNatural(natural.uuid)}>
        <div class="item__title">LanÃ§amento Natural</div>
        <div class="item__content">{natural.title}</div>
        <div class="item__lastchanged">
          {moment().format('HH:MM, DD/MM/YYYY')}
        </div>
      </div>
    {/each}

  </div>

</main>
